.form {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.row {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.label {
  text-align: center;
}

.input {
  border: none;
  border-radius: 20px;
  font-size: 14px;
  line-height: 1.5;
  letter-spacing: 0.02em;
  padding: 0.5rem 1rem;
  transition: box-shadow 200ms ease;
}

.input:focus,
.input:hover {
  border-color: var(--color-accent);
  box-shadow: 0 0 8px var(--color-accent);
  outline: 1px solid var(--color-accent);
}

.submit {
  background-color: var(--color-white);
  border: none;
  border-radius: 20px;
  font-size: 14px;
  letter-spacing: 0.02em;
  line-height: 1.5;
  margin: 2rem auto 0;
  padding: 0.5rem;
  transition: background-color 200ms ease, color 200ms ease;
  width: 50%;
}

.submit:hover,
.submit:focus {
  background-color: var(--color-accent);
  color: white;
  outline: none;
}

@media screen and (min-width: 1024px) {
  .form {
    margin-bottom: 5rem;
  }

  .row {
    align-items: center;
    flex-direction: row;
    gap: 2rem;
    justify-content: space-between;
  }

  .input {
    font-size: 16px;
    width: 70%;
  }

  .submit {
    font-size: 16px;
    width: 30%;
  }
}
