.filter {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.row {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.label {
  text-align: center;
}

.input {
  border: none;
  border-radius: 20px;
  font-size: 14px;
  line-height: 1.5;
  padding: 0.5rem 1rem;
  transition: box-shadow 200ms ease;
}

.input:focus,
.input:hover {
  outline: 1px solid var(--color-accent);
  box-shadow: 0 0 8px var(--color-accent);
}

@media screen and (min-width: 1024px) {
  .row {
    align-items: center;
    flex-direction: row;
    gap: 2rem;
    justify-content: space-between;
  }

  .input {
    font-size: 16px;
    width: 70%;
  }
}
